.BannerContainer {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
}

.Banner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
}

.Banner > span {
    width: 75%;
    text-align: center;
}
